import { Component } from '@angular/core';
import { IonicPage, LoadingController, ModalController, NavController, NavParams } from 'ionic-angular';
import { ScheduleMapper } from '../../../models/schedule.model';
import { ToastProvider } from '../../../providers/toast/toast';
import { PatientHouseholdMonitoringProvider } from '../../../providers/patient-household-monitoring/patient-household-monitoring';
import { MedicalConductProvider } from '../../../providers/medical-conduct/medical-conduct';
import { MedicalConductMapper } from '../../../models/medical-conduct.model';
import {
  DrugsPage,
  ExamClinicSearchPage,
  HospitalSearchPage,
  PatientEvolutionsPage,
  SpecializationClinicSearchPage
} from '../../index';
import { MedicalConductEnum } from '../../../utils/enums';
import { EvolutionsProvider } from '../../../providers/evolutions/evolutions';
import { HospitalMapper } from '../../../models/hospital.model';

/**
 * Generated class for the PatientHouseholdMonitoringPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'PatientHouseholdMonitoringPage'
})
@Component({
  selector: 'page-patient-household-monitoring',
  templateUrl: 'patient-household-monitoring.html',
})
export class PatientHouseholdMonitoringPage {

  public schedule: ScheduleMapper;

  public patientMonitoring: any = {id: '', evolution: {id: '', description: ''},
    medicalConducts: [], hospitalization_reason: '', other_conduct: '', hospital: {}};

  public medicalConductList: MedicalConductMapper[];

  public hospital: HospitalMapper;

  public medicalConductEnum = MedicalConductEnum;

  public clinicSpecializationSelected: any[] = [];

  public clinicExamSelected: any[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private medicalConductPrv: MedicalConductProvider,
              private modalCtrl: ModalController, private toastPrv: ToastProvider, private evolutionsPrv: EvolutionsProvider,
              private patientHouseholdMonitoringPrv: PatientHouseholdMonitoringProvider, public loadingCtrl: LoadingController) {
    this.schedule = this.navParams.get('schedule');
    console.log('this.schedule', this.schedule);
  }

  ionViewDidLoad() {
    this.medicalConductPrv.query().subscribe((medicalConductList: MedicalConductMapper[]) => {
      this.medicalConductList = medicalConductList;
    });

    if (this.schedule.patient_monitoring_id) {
      this.patientHouseholdMonitoringPrv.get(this.schedule.patient_monitoring_id).subscribe((data) => {
        this.patientMonitoring = Object.assign({}, this.patientMonitoring, data)
        if (this.patientMonitoring.clinic_exams) {
          this.clinicExamSelected = this.patientMonitoring.clinic_exams;
        }
        if (this.patientMonitoring.clinic_specializations) {
          this.clinicExamSelected = this.patientMonitoring.clinic_specializations;
        }
      });
    }
  }

  async doPatientMonitoring() {
    if (!this.isValid()) {
      return;
    }

    let loading = this.loadingCtrl.create({
      content: 'Aguarde...'
    });

    loading.present();

    let patientMonitoringPayload = {
      type: 'household',
      id: this.patientMonitoring.id ? this.patientMonitoring.id : null,
      patient_id: this.schedule.patient.id,
      medical_conducts: this.patientMonitoring.medicalConducts ? this.patientMonitoring.medicalConducts : [],
      professional_id: this.schedule.professional.id,
      hospital_id: this.hospital ? this.hospital.id : null,
      hospitalization_reason: this.patientMonitoring.hospitalization_reason,
      clinic_specializations: this.clinicSpecializationSelected ? this.clinicSpecializationSelected : [],
      clinic_exams: this.clinicExamSelected ? this.clinicExamSelected : [],
      other_conduct: this.patientMonitoring.other_conduct,
      schedule_id: this.schedule.id
    };

    this.patientHouseholdMonitoringPrv.save(patientMonitoringPayload).subscribe((patientMonitoring) => {
      this.patientMonitoring.id = patientMonitoring.id;

      // Salva evolução do paciete
      let evolutionPayload = {
        id: this.patientMonitoring.evolution.id ? this.patientMonitoring.evolution.id : '',
        patient_monitoring_id: patientMonitoring.id ? patientMonitoring.id : null,
        description: this.patientMonitoring.evolution.description,
        patient_id: this.schedule.patient.id,
        professional_id: this.schedule.professional.id,
      };
      this.evolutionsPrv.save(evolutionPayload).subscribe((evolution) => {
        this.patientMonitoring.evolution.id = evolution.id;
        loading.dismiss();

        this.toastPrv.show('Monitoramento salvo com sucesso!');
        this.navCtrl.pop();
      }, (err) => {
        console.error(err);
        loading.dismiss();
      });
    }, (err) => {
      console.error(err);
      loading.dismiss();
    });
  }

  isValid(): boolean {

    if (!this.patientMonitoring.evolution.description) {
      this.toastPrv.show('Informe a evolução do paciente');
      return false;
    }

    if (!this.patientMonitoring.medicalConducts.length) {
      this.toastPrv.show('Informe ao menos uma conduta médica');
      return false;
    }

    if (this.patientMonitoring.medicalConducts.indexOf(this.medicalConductEnum.HOSPITAL) >= 0) {
      if (!this.hospital) {
        this.toastPrv.show('Selecione um hospital');
        return false;
      }

      if (!this.patientMonitoring.hospitalization_reason) {
        this.toastPrv.show('Justifique a internação');
        return false;
      }
    }

    if (this.patientMonitoring.medicalConducts.indexOf(this.medicalConductEnum.CLINIC) >= 0) {
      if (!this.clinicSpecializationSelected.length) {
        this.toastPrv.show('Selecione ao menos uma especialidade e clínica');
        return false;
      }
    }

    if (this.patientMonitoring.medicalConducts.indexOf(this.medicalConductEnum.EXAM) >= 0) {
      if (!this.clinicExamSelected.length) {
        this.toastPrv.show('Selecione ao menos um exame e clínica');
        return false;
      }
    }

    return true;
  }

  chooseHospital() {
    let hospitalSearchModal = this.modalCtrl.create(HospitalSearchPage);

    hospitalSearchModal.onDidDismiss((data: any) => {
      this.hospital = data.hospital;
    });

    hospitalSearchModal.present();
  }

  chooseSpecializationClinics() {
    let specializationClinicSearchModal = this.modalCtrl.create(SpecializationClinicSearchPage, {
      'clinicSpecializationSelected' : this.clinicSpecializationSelected
    });

    specializationClinicSearchModal.onDidDismiss((data: any) => {
      this.clinicSpecializationSelected = data.clinicSpecializationSelected;
      console.log('this.clinicSpecializationSelected', this.clinicSpecializationSelected);
    });

    specializationClinicSearchModal.present();
  }

  chooseClinicsExams() {
    let examClinicSearchPage = this.modalCtrl.create(ExamClinicSearchPage, {
      'clinicExamSelected' : this.clinicExamSelected
    });

    examClinicSearchPage.onDidDismiss((data: any) => {
      this.clinicExamSelected = data.clinicExamSelected;
      console.log('this.clinicExamSelected', this.clinicExamSelected);
    });

    examClinicSearchPage.present();
  }

  searchDrugs() {
    let drugSearchModal = this.modalCtrl.create(DrugsPage);

    drugSearchModal.present();
  }

  evolutions() {
    this.navCtrl.push(PatientEvolutionsPage, {
      patient: this.schedule.patient
    });
  }

}



